.headerImgW {
    border-radius: 50%;
    //display: inline-block;
    display: flex;
    position: relative;
    justify-content: center;
}

.headerImgW img {
    border-radius: 50%;
    object-fit: contain;
    height: auto;
}

.headerImgW img.no-radius {
    border-radius: 0%;
}

.headerImgW:after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    position: absolute;
    top: 0; left: 0;
}

.headerImgW.no-radius {
    border-radius: 0%;
}

/* Responsive Tiled Photo Gallery */
.galleryWrap
{
        overflow: hidden;
        margin: 10px;
}

.pictureBox
{
        float: left;
        position: relative;
        width: 33.3%;
        padding-bottom: 33.3%;
}

.innerBox
{
        position: absolute;
        left: 10px;
        right: 10px;
        top: 10px;
        bottom: 10px;
        overflow: hidden;
}

.innerBox img
{
        width: 100%;
}

.innerBox .titleBox
{
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        margin-bottom: -50px;
        background: #000;
        background: rgba(0, 0, 0, 0.5);
        color: #FFF;
        padding: 10px;
        text-align: center;
        -webkit-transition: all 0.3s ease-out;
        -moz-transition: all 0.3s ease-out;
        -o-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;
}
.innerBox:hover .titleBox
{
        margin-bottom: 0;
}
@media only screen and (max-width : 480px)
{
        /* Smartphone view: 1 tile */
        .pictureBox
        {
        width: 100%;
        padding-bottom: 100%;
        }
}
@media only screen and (max-width : 650px) and (min-width : 481px)
{
        /* Tablet view: 2 tiles */
        .pictureBox
        {
        width: 50%;
        padding-bottom: 50%;
        }
}
@media only screen and (max-width : 1050px) and (min-width : 651px)
{
        /* Small desktop / ipad view: 3 tiles */
        .pictureBox
        {
        width: 33.3%;
        padding-bottom: 33.3%;
        }
}
@media only screen and (max-width : 1290px) and (min-width : 1051px)
{
        /* Medium desktop: 4 tiles */
        .pictureBox
        {
        width: 33.3%;
        padding-bottom: 33.3%;
        }
}
